@font-face {
  font-family: "Spartan";
  src: url("./fonts/Spartan.ttf");
}

body {
  margin: 0;
  padding: 0;
}

.App {
  margin: auto;
  padding: auto;
  background-color: #f5b9c1;
  overflow: hidden;
}

.header {
  display: flex;
  align-items: center;
  width: 100vw;
  height: 100vh;

  margin: auto;
}

img {
  display: flex;
  position: absolute;
  align-items: center;
  justify-content: center;
  width: 66%;
  user-select: none;
}

.Tilt {
  cursor: crosshair;
  user-select: none;
  transform: translateZ(-800px);
  display: flex;
  align-items: center;
  justify-content: center;
  margin: auto;
  width: 100%;
  height: 100%;
  /* background-color: #F5B9C1; */
  /* background-image: linear-gradient(150deg, #607D8B 0%, #9C27B0 100%, #ff1ff7 100%); */
  /* box-shadow: 0 20px 27px rgba(0, 0, 0, 0.05); */
  transform-style: preserve-3d;
  transition-timing-function: ease-in-out;
}

.Tilt-inner {
  user-select: none;

  /* border: 1px dashed white; */
  align-items: center;
  justify-content: center;
}

